<template >
  <div class="two mescroll">
    <div class="top">
      <img src="@/assets/img/card/two_top.png" alt="">
    </div>
    <div class="mid">
      <img class="mid_top" src="@/assets/img/card/two_top1.png" alt="">
      <van-cell-group>
        <van-field v-model="name" label="姓名" placeholder="请输入姓名" @blur="blur" />
        <van-field v-model="telno" type="number" label="手机号" placeholder="请输入手机号" @blur="blur" />
        <van-cell title="收货地址" is-link arrow-direction="down" :value-class="areaName?'color':''" :value="areaName || '请选择城市'" @click="showAddress=true" />
        <van-field v-model="addr" label="详细地址" placeholder="请输入地址" @blur="blur" />
      </van-cell-group>
      <div class="btn">
        <img src="@/assets/img/card/two_btn.png" @click="submit" alt="">
        <p class="text">(每位用户限领一张)</p>
      </div>
    </div>
    <div class="bottom">
      <img src="@/assets/img/card/two_bottom.png" alt="">
    </div>
    <van-popup v-model="showAddress" position="bottom">
      <van-area :area-list="areaList" :value="areaCode" @confirm="areaConfirm" @cancel="showAddress = false" />
    </van-popup>
  </div>
</template>
<script>
import { CellGroup, Cell, Field, Popup, Area } from "vant";
import areaList from "@/utils/AreaList";
import { cardSubmit } from "@/api";
export default {
  components: {
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Field.name]: Field,
    [Popup.name]: Popup,
    [Area.name]: Area,
  },
  data() {
    return {
      name: "",
      telno: "",
      areaName: "",
      areaCode: "",
      addr: "",
      areaList: areaList,
      showAddress: false,
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    areaConfirm(obj) {
      this.showAddress = false;
      this.areaName = `${obj[0].name} ${obj[1].name} ${obj[2].name}`;
      this.areaCode = obj[2].code;
    },
    submit() {
      if (!this.name) {
        this.$toast("请输入姓名");
        return;
      }
      if (!this.telno) {
        this.$toast("请输入手机号");
        return;
      }
      if (!this.$utils.isMobile(this.telno)) {
        this.$toast("请输入正确的手机号");
        return;
      }
      if (!this.areaCode) {
        this.$toast("请选择省市区");
        return;
      }
      if (!this.addr) {
        this.$toast("请输入详细收货地址");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      cardSubmit({
        gcode: "1293102453984354304",
        username: this.name,
        usertel: this.telno,
        userIDCard: "",
        addrCode: this.areaCode,
        addrAddr: this.addr,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast("领取成功");
          this.name = "";
          this.telno = "";
          this.areaName = "";
          this.areaCode = "";
          this.addr = "";
        } else {
          this.$toast(res.msg);
        }
      });
    },
  },
  mounted() {
    document.title = "大王卡";
  },
};
</script>
<style lang="scss" scoped>
.two {
  background: #28285b;
  .top {
    img {
      width: 100%;
      display: block;
    }
  }
  .mid {
    margin: 0 20px;
    padding: 0 0 10px;
    border: 3px solid #ff8c00;
    border-radius: 10px;
    background: #fff;
    overflow: hidden;
    &_top {
      margin: 5px;
      width: calc(100% - 10px);
    }
    .van-cell__title {
      width: 90px;
      flex: none;
    }
    .van-cell__value {
      text-align: left;
    }
    .color {
      color: #111;
    }

    .btn {
      margin: 10px 30px;
      img {
        width: 100%;
      }
      .text {
        color: #ff492a;
        text-align: center;
        line-height: 20px;
        font-size: 13px;
      }
    }
  }
  .bottom {
    img {
      width: 100%;
      display: block;
    }
  }
}
</style>

